<template>
  <div class="container mt-10">
    <!---->
    <v-btn class="mx-2 fixed-top backbtn" fab dark small color="primary" @click="goBack">
      <v-icon dark>mdi-arrow-left</v-icon>
    </v-btn>

    <!-- -->
    <v-snackbar v-model="snackbar">
        .
      <template v-slot:action="{ attrs }">
        <v-btn color="pink" text v-bind="attrs" @click="snackbar = false"></v-btn>
        <v-btn color="blue" text v-bind="attrs" @click="realBack"></v-btn>
      </template>
    </v-snackbar>

    <div v-if="isLogin">
      <h2>  </h2>
      <form class="create-form">
        <div class="form-group">
          <label for="roomTitle">  </label>
          <input type="text" class="form-control roomTitle" id="roomTitle" v-model="studyroom.roomTitle" required>
          <small class="form-text text-muted">   .</small>
        </div>
        <div class="form-group">
          <label for="licenseId"> </label>
          <select multiple class="form-control" v-model="selected2" required>
            <option v-for="license in licenseArray" :key="license.id">{{ license.licenseName }}</option>
          </select>
          <small class="form-text text-muted">  .</small>
          <p v-if="this.selected2 != ''"><span class="text-primary">{{ selected2[0] }}</span>() .</p>
        </div>
        <div class="form-group">
          <label for="testDate"> </label>
          <input type="date" class="form-control testDate" id="testDate" v-model="studyroom.testDate" required>
          <small class="form-text text-muted">   YYYY-MM-DD  . ex) 2020-07-12 </small>
        </div>
        <div class="custom-control custom-switch form-group">
          <input type="checkbox" class="custom-control-input" id="isPrivate" v-model="studyroom.isPrivate" @checked="changePrivate">
          <label class="custom-control-label isPrivate" for="isPrivate"> </label>
        </div>
        <div class="form-group" v-show="this.studyroom.isPrivate">
          <label for="roomPassword"> </label>
          <input type="password" class="form-control roomPassword" id="roomPassword" v-model="studyroom.roomPassword">
          <small class="form-text text-muted"> .</small>
        </div>
        <div>

          <!-- -->
          <label for="calendar"></label>
          <div style="width: 100%">
            <v-date-picker v-model="dates" multiple :landscape="landscape" :reactive="reactive" :fullWidth="fullWidth" @click:date="clickdate" mode="multiple"></v-date-picker>
            <v-dialog v-model="dialog" persistent max-width="290">
              <v-card class="pa-3">
                <v-form>
                  <v-text-field v-model="todoDate" :counter="10" label=" ." required></v-text-field>
                  <v-text-field v-model="todoContent" :counter="30" label=" " required></v-text-field>
                </v-form>
                <v-spacer></v-spacer>
                <v-card-actions class="d-flex justify-content-between mt-2">
                  <!-- <v-btn color="red darken-3" @click="modalClose"></v-btn> -->
                  <v-btn color="red darken-3" fab small dark @click="modalClose"><v-icon>mdi-arrow-left-bold</v-icon></v-btn>
                  <!-- <v-btn color="indigo darken-3" @click="modalSave"></v-btn> -->
                  <v-btn color="primary" fab small dark @click="modalSave"><v-icon>mdi-pencil</v-icon></v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </div>

          <div class="">
            <v-card v-scroll.self="onScroll" class="overflow-y-auto" max-height="170">
              <v-text v-model="this.todothings">
                  <v-chip v-for="todo in this.todothings" :key="todo" class="ma-2 indigo darken-1">
                    <span class="badge badge-light mr-2">{{ todo.todoDate }}</span>
                    <!-- <v-chip left class="blue darken-4">{{ todo.todoDate }}</v-chip> -->
                    <span class="text-white">{{ todo.todoContent }}</span>
                    <v-avatar right @click="deltodo(todo)"><v-icon color="white">mdi-close-circle</v-icon></v-avatar>
                  </v-chip>
              </v-text>
            </v-card>
          </div>

        </div>
        <div class="form-group mt-3">
            <div class="custom-control custom-switch form-group">
              <input type="checkbox" class="custom-control-input" id="isAlone" v-model="isAlone">
              <label class="custom-control-label isAlone" for="isAlone"> </label>
            </div>
          <div v-if="isAlone == false">
            <label for="roomHashtag"></label>
            <input type="text" class="form-control maxMembers" v-model="studyroom.maxMembers">
            <small class="form-text text-muted">  .  . ex) 20</small>
          </div>
        </div>
        <div class="form-group">
          <label for="roomHashtag"> </label>
          <input type="text" class="form-control roomGoal" v-model="studyroom.roomGoal">
          <small class="form-text text-muted">  .</small>      
        </div>
        <div class="form-group">
          <label for="roomInfo">  </label>
          <textarea class="form-control" id="roomInfo" 
          placeholder="        . ex)  2" v-model="studyroom.roomInfo" required></textarea>
        </div>

        <!-- -->
        <div>
          <label for="hashtag"> </label>
          <v-combobox v-model="model" :filter="filter" :hide-no-data="!search"
            :items="items" :search-input.sync="search" hide-selected label="Search for an option"
            multiple small-chips solo>
            <template v-slot:no-data>
              <v-list-item>
                <span class="subheading">Create</span>
                <v-chip :color="`${colors[nonce - 1]} lighten-3`" label small >
                  {{ search }}
                </v-chip>
              </v-list-item>
            </template>
            <template v-slot:selection="{ attrs, item, parent, selected }">
              <v-chip v-if="item === Object(item)"  v-bind="attrs" class="indigo "
                :input-value="selected" label small>
                <span class="pr-2 text-white">
                  {{ item.text }}
                </span>
                <v-icon small @click="parent.selectItem(item)" color="white" right>mdi-close-circle</v-icon>
              </v-chip>
            </template>
            <template v-slot:item="{ index, item }">
              <v-text-field
                v-if="editing === item"
                v-model="editing.text" autofocus flat
                background-color="transparent" hide-details solo @keyup.enter="edit(index, item)"></v-text-field>
              <v-chip v-else :color="`${item.color} lighten-3`" dark label small>
                {{ item.text }}
              </v-chip>
              <v-spacer></v-spacer>
              <v-list-item-action @click.stop>
                <v-btn icon @click.stop.prevent="edit(index, item)">
                  <v-icon>{{ editing !== item ? 'mdi-pencil' : 'mdi-check' }}</v-icon>
                </v-btn>
              </v-list-item-action>
            </template>
          </v-combobox>
        </div>
      </form>
      <button class="btn btn-success submit-btn" @click="submit">  </button>
    </div>
    <div v-if="!isLogin" class="notLogin text-center">
      <h5> </h5>
      <v-btn @click="goLogin" class="mt-5"></v-btn>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'RoomCreate',
  data() {
    return {
      snackbar: false,
      dates: new Date().toISOString().substr(0, 10),
      landscape: false,
      reactive: false,
      fullWidth: true,
      UID: this.$store.state.member.loginUID,
      studyroom: {
        captinId: this.$store.state.member.loginUID,
        roomTitle: '',
        testDate: '',
        licenseId: '',
        isPrivate: false,
        roomPassword: '',
        dateForStudyroom: [],
        maxMembers: '',
        roomGoal: '',
        roomInfo: '',
        roomHashtag: [],
      },
      // inputHash: '',
      dialog: false,
      todoDate: '',
      todoContent: '',  
      licenseArray: '',
      selected2: [],
      content: '',
      todothings: [],
      dateall: [],
      isAlone: false,
      

      // 
      activator: null,
      attah: null,
      colors: ['blue'],
      editing: null,
      index: -1,
      items: [
        { header: ' ' },
        {
          text: '',
          color: 'blue',
        },
      ],
      nonce: 1,
      menu: false,
      model: [
        {
          text: '',
          color: 'blue',
        },
      ],
      x: 0,
      search: null,
      y: 0,
    }
  },
  computed: {
    isLogin() {
      return this.$store.state.member.isLogin
		}
  },
  methods: {
    onScroll () {
      this.scrollInvoked++
    },
    goBack() {
      if(this.studyroom.roomTitle.length >= 1 ||  this.studyroom.dateForStudyroom.length >= 1
      || this.studyroom.roomGoal.length >= 1 || this.studyroom.roomInfo.length >= 1){
        this.snackbar = true
      }
      else {
        this.$router.push('/rooms')
      }
    },
    realBack() {
      this.$router.push('/rooms')
    },
    submit() {
      //  
      if (this.studyroom.isPrivate == false) {
        this.studyroom.roomPassword = ''
      }
      //  
      if (this.isAlone == true) {
        this.studyroom.maxMembers = 1
      }
      //  
      for(var i=0; i<this.model.length; i++) {
        if(this.model[i])
        this.studyroom.roomHashtag.push({"hashtag" : this.model[i]["text"]})
      }
      //  
      this.studyroom.dateForStudyroom = this.todothings
      axios.post(`http://${this.$store.state.address}:${this.$store.state.port}/study/createStudyroom`, this.studyroom)
      .then(response => {
        console.log(response)
        console.log(this.studyroom)
        this.$router.push({name: 'Rooms'})
      }).catch(error => {
        console.log(error)
        this.model = []
        this.studyroom.roomHashtag = []
      })
      // console.log(this.studyroom)
      // this.model = []
      // this.studyroom.roomHashtag = []
    },
    //  
    clickdate(event) {
      this.todoDate = event
      this.dialog = true
    },
    modalClose() {
      this.todoContent = ''
      this.dialog = false
      console.log(this.todothings.todoDate)
      this.dates = this.dateall
    },
    modalSave() {
      if (this.todoContent != '') {
        this.dialog = false
        this.todothings.push({"todoDate" : this.todoDate, "todoContent" : this.todoContent})
        console.log(this.todothings)
        this.dateall.push(this.todoDate)
        this.dates = this.dateall
        this.todoContent = ''
      }
    },
    //  
    deltodo(todo) {
      // todothings  
      const idx = this.todothings.indexOf(todo)
      if (idx > -1) this.todothings.splice(idx, 1)
      const i = this.dateall.indexOf(todo.todoDate)
      this.dateall.splice(i, 1)
      this.dates = this.dateall
    },
    changePrivate() {
      this.studyroom.isPrivate != this.studyroom.isPrivate
    },
    edit (index, item) {
      if (!this.editing) {
        this.editing = item
        this.index = index
      } else {
        this.editing = null
        this.index = -1
      }
    },
    filter (item, queryText, itemText) {
      if (item.header) return false

      const hasValue = val => val != null ? val : ''

      const text = hasValue(itemText)
      const query = hasValue(queryText)

      return text.toString()
        .toLowerCase()
        .indexOf(query.toString().toLowerCase()) > -1
    },
  },
  created() {
    axios.get(`http://${this.$store.state.address}:${this.$store.state.port}/license/getAll`)
    .then(response => {
      console.log(response.data.object)
      this.licenseArray = response.data.object
    })
  },
  watch: {
    selected2() {
      for(var idx=0; idx<this.licenseArray.length; idx++) {
        if(this.licenseArray[idx].licenseName == this.selected2) {
          this.studyroom.licenseId = this.licenseArray[idx].id
        }
      }
    },
    model (val, prev) {
      if (val.length === prev.length) return

      this.model = val.map(v => {
        if (typeof v === 'string') {
          v = { text: v, color: this.colors[0],}
          this.items.push(v)
          this.nonce++
        }
        return v
      })
    },
  }
}
</script>

<style scoped>
.backbtn {
  z-index: 8;
  position: fixed;
  top: 65px
}
.create-form {
  margin-top: 30px;
}
.form-group {
  text-align: left;
}
.submit-btn {
  width: 100%;
}
</style>