<template>
<v-container fill-height style="max-width:350px">
  <v-layout align-center row wrap>
    <v-flex xs12>      
      <v-toolbar flat>
        <v-toolbar-title></v-toolbar-title>
      </v-toolbar>
      <div class="pa-3">
        <div class="d-flex align-center row pa-3">
          <v-text-field
            v-model="signupData.userEmail"
            label=""
          ></v-text-field>
          <v-btn :disabled="inActive" @click="submit" class="ml-3" color="primary"></v-btn>
        </div>
        <!--    -->
        <div v-if="isSendEmail" class="d-flex align-center row pa-3">
          <v-text-field
            v-model="inputCode"
            label=" "
          ></v-text-field>
          <v-btn @click="certify" class="ml-3" color="primary" :disabled="emailComplete"></v-btn>        </div>

        <div class="error-text" v-if="error.email">{{error.email}}</div>

        <v-text-field
          v-model="signupData.userName"
          label=""
        ></v-text-field>
        <v-text-field
          v-model="signupData.password"
          label=""
          type="password"
        ></v-text-field>
        <div class="error-text" v-if="error.password">{{error.password}}</div>
        <v-text-field
          v-model="passwordConfirm"
          label=" "
          type="password"
        ></v-text-field>
        <div class="error-text" v-if="error.passwordConfirm">{{error.passwordConfirm}}</div>
      <div class="my-2">
        <v-btn block large color="primary" @click="signup(signupData)" :disabled="!isSubmit"></v-btn>

      </div>
    </div>
    </v-flex>
  </v-layout>
</v-container>
</template>

<script>
import axios from "axios"
import { mapActions } from "vuex"
import * as EmailValidator from "email-validator";
import PV from "password-validator";

export default {
  name: 'Signup',
  data() {
    return {
      signupData : {
        userEmail: null,
        userName: null,
        password: null,
      },
      passwordConfirm: null,
      passwordSchema: new PV(),
      error: {
        email: false,
        password: false,
        passwordConfirm: false
      },
      isSubmit: false,
      readyToCertify: false,
      isSendEmail: false,
      certifyCode: null,
      inputCode: null,
      emailComplete: false,
      inActive: false
    }
  },
  created() {
    this.component = this;

    this.passwordSchema
      .is()
      .min(8)
      .is()
      .max(100)
      .has()
      .digits()
      .has()
      .letters()
  },
  watch: {
    'signupData.userEmail': function() {
      this.checkEmail()
      this.checkFormConfirm()

    },
    'signupData.password': function() {
      this.checkPassword()
      this.checkFormConfirm()

    },
    passwordConfirm: function() {
      this.checkPasswordConfirm()
      this.checkFormConfirm()
    },
    'signupData.userName' : function() {
      this.checkFormConfirm()
    }
  },
  methods: {
    ...mapActions(["signup"]),
    submit() {
      this.inActive = true
      //      
      axios.post('http://${state.address}:8080/checkemail', {
        userEmail: this.signupData.userEmail
      })
      .then( res => {
        console.log(res)
        // certifyCode  
        this.certifyCode = res.data.object
        //   ,   
        this.isSendEmail = true
        // 
        alert(' .')
        this.inActive = false
      })
      .catch( function() {
        // alert     
        alert('  .')
      })
    },
    certify() {
      //   
      if (this.certifyCode == this.inputCode){
        this.emailComplete = true
        alert('  .')
        this.inActive = true
        // console.log('')
      } else {
        //   
        // console.log(' ')
        alert(' ')
      }
  
    },
    checkEmail() {
      if (this.signupData.userEmail.length >= 0 && !EmailValidator.validate(this.signupData.userEmail)){
        this.readyToCertify = false
        this.error.email = "  ."
        }
      else {
        this.error.email = false
        this.readyToCertify = true
      }
    },
    checkPassword () {
      if (
        this.signupData.password.length >= 0 &&
        !this.passwordSchema.validate(this.signupData.password)
      )
        this.error.password = ",  8  ."
      else this.error.password = false
    },
    checkPasswordConfirm () {
      if (this.passwordConfirm.length >= 0 &&
      this.signupData.password != this.passwordConfirm
      )
        this.error.passwordConfirm = "  ."
      else this.error.passwordConfirm = false
    },
    checkFormConfirm () {
      // ,    x 
      let checkError = true
      Object.values(this.error).map(v => {
        if (v) {
          checkError = false
        }
      })
      this.isSubmit = checkError
      //    
      if(this.isSubmit && this.emailComplete && this.signupData.userName && this.signupData.userEmail && this.signupData.password && this.passwordConfirm) {
        this.isSubmit = true
      } else {
        this.isSubmit = false
      }
    }
  }
}
</script>

