<template>
  <v-card>
    <v-card-title>
      <span class="headline"> </span>
    </v-card-title>
    <v-card-text>
      <v-container>
        <v-row>
          <v-col cols="12" sm="6" md="4">
            <v-text-field label="*" required v-model="licenseTitle"></v-text-field>
            <v-btn @click="searchLicense"></v-btn>
          </v-col>
          <v-col cols="12" sm="6">
            <v-select
              v-model="myLicenseData.licenseStatus"
              :items="['todo', 'doing', 'pass']"
              label=" *"
              required
            ></v-select>
          </v-col>
          <!-- pass -->
          <v-col v-if="myLicenseData.licenseStatus === 'pass'" cols="12" sm="6" md="4">
            <v-text-field
              v-model="myLicenseData.licenseScore"
              label=""
              hint="   ."
              persistent-hint
            ></v-text-field>
          </v-col>
          <v-col v-if="myLicenseData.licenseStatus === 'pass'" cols="12" sm="6" md="4">
            <v-text-field
              v-model="myLicenseData.licenseGrade"
              label=""
              hint="   ."
              persistent-hint
            ></v-text-field>
          </v-col>
          <v-col v-if="myLicenseData.licenseStatus === 'pass'" cols="12">
            <p>   .</p>
            <form>
              <input v-model="myLicenseData.dueData" type="date">
            </form>
          </v-col>
          <v-col v-if="myLicenseData.licenseStatus === 'pass'" cols="12">
            <p>   .</p>
            <form>
              <input v-model="myLicenseData.gainDate" type="date">
            </form>
          </v-col>

          <!-- todo/doing -->
          <v-col v-if="myLicenseData.licenseStatus !== 'pass'" cols="12" sm="6" md="4">
            <v-text-field
              v-model="myLicenseData.licenseScore"
              label=""
              hint="   ."
              persistent-hint
            ></v-text-field>
          </v-col>
          <v-col v-if="myLicenseData.licenseStatus !== 'pass'" cols="12" sm="6" md="4">
            <v-text-field
              v-model="myLicenseData.licenseGrade"
              label=""
              hint="   ."
              persistent-hint
            ></v-text-field>
          </v-col>
          <v-col v-if="myLicenseData.licenseStatus !== 'pass'" cols="12">
            <p>   .</p>
            <form>
              <input v-model="myLicenseData.testDate" type="date">
            </form>
          </v-col>
        </v-row>
      </v-container>
      <small>*  .</small>
    </v-card-text>
    <v-card-actions>
      <v-spacer></v-spacer>
      <v-btn color="blue darken-1" text @click="back">Close</v-btn>
      <v-btn color="blue darken-1" text @click="saveMyLicense; back()">Save</v-btn>
    </v-card-actions>
  </v-card>
</template>

<script>
import axios from 'axios'

export default {
  name: 'MyLicenseCreate',
  data() {
    return {
      "dialog1": false,
      "notifications": false,
      "sound": true,
      "widgets": false,
      myLicenseData: {
        uid: this.hostId,
        licenseId: 1,
        licenseStatus: null,
        licenseScore: null,
        licenseGrade: null,
        gainDate: null,
        dueData: null,
        testDate: null
      },
      licenseTitle: null,
    }
  },
    props: {
    hostID : {
      type: Number
    }
  },
  methods: {
    saveMyLicense(){
      axios.post('http://${state.address}:${this.$store.state.port}/license/addMyLicense', this.myLicenseData)
      .then( res => {
        console.log(res)
      })
      .catch( res => {
        console.log(res)
      })
    },
    searchLicense(){
      console.log(this.licenseTitle)
      axios.get('http://${state.address}:${this.$store.state.port}/license/getByKeyword', {
        params: 
        {
          keyword: this.licenseTitle
        }
      })
      .then (res => {
        console.log(res.data)
        const selectedLicenseList = res.data
        this.$store.state.license.selectedLicense = selectedLicenseList
        this.$router.push('/license/result')
      })
      .catch(err => console.log(err.message))
    },
    back() {
      this.$router.go(-1)
    }
  }

}
</script>

